import React from 'react';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

const Selection = (classes) => {
    const [diagram, setDiagram] = React.useState(localStorage.getItem('vis') === null || localStorage.getItem('vis') === "bars" ? "" : localStorage.getItem('vis'));

    const handleDiagram = (event) => {
        setDiagram(event.target.value);

        switch (event.target.value) {
            case "":
                localStorage.setItem('vis', "bars");
                break;
            case "doubleBars":
                localStorage.setItem('vis', "doubleBars");
                break;
            case "sidebars":
                localStorage.setItem('vis', "sidebars");
                break;
            case "circle":
                localStorage.setItem('vis', "circle");
                break;
            case "multiColor":
                localStorage.setItem('vis', "multiColor");
                break;
            default:
                localStorage.setItem('vis', "bars");
        }
    };

    return (
        <FormControl style={{ minWidth: "180px", marginLeft: "30px", marginRight: "30px" }} className={classes.formControl}>
            <InputLabel shrink id="demo-simple-select-placeholder-label-label">
                Diagram â€ŽType
            </InputLabel>
            <Select
                labelId="demo-simple-select-placeholder-label-label"
                id="demo-simple-select-placeholder-label"
                value={diagram}
                onChange={handleDiagram}
                displayEmpty
                className={classes.selectEmpty}
            >
                <MenuItem value={""}>Bars</MenuItem>
                <MenuItem value={"doubleBars"}>Double Bars</MenuItem>
                <MenuItem value={"sidebars"}>Sidebars</MenuItem>
                <MenuItem value={"circle"}>Circle</MenuItem>
                <MenuItem value={"multiColor"}>Multi Color</MenuItem>
            </Select>
        </FormControl>
    );
}


export default Selection;