import React from 'react';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Slider from '@material-ui/core/Slider';


const BarRange = (classes) => {
    const first = localStorage.getItem('barRange') === null ? 0 : Number(localStorage.getItem('barRange').split(",")[0]);
    const last = localStorage.getItem('barRange') === null ? 100 : Number(localStorage.getItem('barRange').split(",")[1]);

    const [value, setValue] = React.useState([first, last]);

    const handleChange = (event, newValue) => {
        setValue(newValue);
        if (localStorage.getItem('barRange') != value) {
            localStorage.setItem('barRange', value);
        }
    };

    const marks = [
        {
            value: 1,
            label: '1',
        },
        {
            value: 100,
            label: '100',
        },
    ];

    return (
        <FormControl className={classes.formControl} style={{ minWidth: "180px", marginLeft: "30px", marginRight: "30px" }}>
            <InputLabel shrink id="demo-simple-select-placeholder-label-label">
                Bar Range
            </InputLabel>
            <Slider style={{ marginTop: "24px" }}
                value={value}
                onChange={handleChange}
                valueLabelDisplay="auto"
                aria-labelledby="range-slider"
                marks={marks}
            />
        </FormControl>
    );
}


export default BarRange;