import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import BarMultiple from './BarMultiple';
import DiagramType from './DiagramType';
import BarColor from './BarColor';
import BackgroundColor from './BackgroundColor';
import BarRange from './BarRange';
import Height from './Height';

const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
}));

const Selection = () => {
    const classes = useStyles();

    return (
        <div style={{ margin: "20px" }}>
            <DiagramType classes={classes} />
            <BarMultiple classes={classes} />
            <BarRange classes={classes} />
            <BarColor classes={classes} />
            <BackgroundColor classes={classes} />
            <Height />
        </div>
    );
}


export default Selection;